.App {
  min-height: 100vh;
  text-align: center;
  background-image: linear-gradient(to top, #d299c2 0%, #fef9d7 100%);
  padding: 50px;
}

.search-form{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  margin: 50px auto;
}

.search-bar{
  display: flex;
  width: 100%;
  border:none;
  padding: 10px;
  font-family: Montserrat;
  font-weight: normal;
  border-radius: 20px;
  margin-left: 5px;
}

@media (max-width: 768px){
  .search-bar{
  min-width: 100%;  
  }
}

.search-button{
  border:none;
  padding: 10px 20px;  
}

.header{
  display: inline-flex;
}
.header h1{
  font-weight: 400;
 
}
.logo-header{
  width:60px;
  height: 60px;
  margin-right: 10px; 
}
.background-icon-1{
  position: absolute;
  width:500px;
  height:500px;
  top: 250px;
  right:150px;
}
.background-icon-2{
  position: absolute;
  width:500px;
  height: 500px;
  bottom:80px;
  left:150px;
}
.recipes{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}




 
