
  .recipe {
     border-radius: 10px;
     box-shadow: 0px 5px 20px lightgrey;
     margin: 20px;
     min-width: 40%;
     display: flex;
     flex: 1;
     flex-direction: column;
     align-items: center;
     background-color: white;   
 }

 @media (max-width: 768px){
    .recipe{
    min-width: 100%;  
    }
}

 h3 {
     width: 50%;
 }
 ul {
    list-style-type: none;
    font-weight: 400;
    font-size: 0.75rem;
    padding: 0 10px;
 }

 li {
     margin: 0 20px;
 }

 .image {
     width:60%;
     height: auto;
     object-fit: cover;
 }
 @media (max-width: 768px){
    .image{
        width: 70%;
    }
}

 .ingredient_title{
     margin: 10px 0;
 }
 .ingredient{
    text-align: left;
    width: 60%;
    margin: 0 100px;
 }
 .ingredientSearch{
     margin: 0px;  
 }
 .ingredientMissed {
     margin-top: 0px ;
     margin-bottom: 12px; 
 }